import React from 'react';
import { createRoot } from 'react-dom/client';
import { BrowserRouter, createRoutesFromElements, createBrowserRouter, RouterProvider } from 'react-router-dom';
import './App.css';
import App from './App.jsx';

// Material-UI v4  findDOMNode  React 18 
//  findDOMNode 
const originalConsoleError = console.error;
console.error = (...args) => {
  if (args[0]?.includes?.('findDOMNode') || String(args[0])?.includes?.('findDOMNode')) {
    return;
  }
  originalConsoleError(...args);
};

//  React 18  createRoot API
const root = createRoot(document.getElementById('root'));
root.render(
  <BrowserRouter future={{ v7_startTransition: true, v7_relativeSplatPath: true }}>
    <App />
  </BrowserRouter>
);