import React, { useState } from 'react';
import {
  Typography,
  Box,
  Paper,
  Tabs,
  Tab,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Card,
  CardContent,
  CardActions,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { MusicNote, LibraryMusic, Equalizer, ArrowBack, EmojiEvents } from '@material-ui/icons';

// 
import ScaleIdentification from './ScaleIdentification';
import ChordMaster from './ChordMaster';
import RhythmTraining from './RhythmTraining';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    marginBottom: theme.spacing(4),
  },
  gameContainer: {
    marginTop: theme.spacing(3),
  },
  levelCard: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    transition: 'transform 0.2s ease',
    '&:hover': {
      transform: 'translateY(-5px)',
      boxShadow: '0 8px 16px rgba(0,0,0,0.1)',
    },
  },
  levelCardContent: {
    flexGrow: 1,
  },
  levelCardActions: {
    justifyContent: 'center',
  },
  backButton: {
    marginBottom: theme.spacing(2),
  },
  gameIcon: {
    fontSize: 40,
    marginBottom: theme.spacing(1),
    color: theme.palette.primary.main,
  },
  resultContainer: {
    textAlign: 'center',
    padding: theme.spacing(3),
  },
  resultIcon: {
    fontSize: 60,
    color: theme.palette.primary.main,
    marginBottom: theme.spacing(2),
  },
}));

const GameSelector = () => {
  const classes = useStyles();
  const [selectedGame, setSelectedGame] = useState(0);
  const [selectedLevel, setSelectedLevel] = useState(null);
  const [showLevelSelection, setShowLevelSelection] = useState(false);
  const [gameResult, setGameResult] = useState(null);
  const [showResult, setShowResult] = useState(false);

  // 
  const games = [
    {
      id: 'scale',
      name: '',
      description: '',
      icon: <MusicNote className={classes.gameIcon} />,
      component: ScaleIdentification,
      levels: [
        { level: 1, name: '', description: '' },
        { level: 2, name: '', description: '' },
        { level: 3, name: '', description: '' },
      ],
    },
    {
      id: 'chord',
      name: '',
      description: '',
      icon: <LibraryMusic className={classes.gameIcon} />,
      component: ChordMaster,
      levels: [
        { level: 1, name: '', description: '' },
        { level: 2, name: '', description: '' },
        { level: 3, name: '', description: '' },
      ],
    },
    {
      id: 'rhythm',
      name: '',
      description: '',
      icon: <Equalizer className={classes.gameIcon} />,
      component: RhythmTraining,
      levels: [
        { level: 1, name: '', description: '' },
        { level: 2, name: '', description: '' },
        { level: 3, name: '', description: '' },
      ],
    },
  ];

  // 
  const handleGameChange = (event, newValue) => {
    setSelectedGame(newValue);
    setSelectedLevel(null);
    setShowLevelSelection(false);
    setGameResult(null);
    setShowResult(false);
  };

  // 
  const handleStartGame = () => {
    setShowLevelSelection(true);
  };

  // 
  const handleLevelSelect = (level) => {
    setSelectedLevel(level);
    setShowLevelSelection(false);
  };

  // 
  const handleBackToGameSelection = () => {
    setSelectedLevel(null);
    setGameResult(null);
    setShowResult(false);
  };

  // 
  const handleBackToLevelSelection = () => {
    setSelectedLevel(null);
    setShowLevelSelection(true);
    setGameResult(null);
    setShowResult(false);
  };

  // 
  const handleGameComplete = (result) => {
    setGameResult(result);
    setShowResult(true);
  };

  // 
  const handlePlayAgain = () => {
    setShowResult(false);
    setGameResult(null);
  };

  // 
  const handleTryNewLevel = () => {
    setSelectedLevel(null);
    setShowLevelSelection(true);
    setGameResult(null);
    setShowResult(false);
  };

  // 
  const renderGameResult = () => {
    if (!gameResult) return null;

    const { score, totalQuestions, level } = gameResult;
    const percentage = Math.round((score / totalQuestions) * 100);
    const isPassed = percentage >= 70;

    return (
      <Box className={classes.resultContainer}>
        <EmojiEvents className={classes.resultIcon} />
        <Typography variant="h5" gutterBottom>
          
        </Typography>
        <Typography variant="h6" gutterBottom>
          : {score} / {totalQuestions} ({percentage}%)
        </Typography>
        <Typography variant="body1" paragraph>
          {isPassed
            ? ''
            : '70%'}
        </Typography>
        <Box mt={3}>
          <Button
            variant="contained"
            color="primary"
            onClick={handlePlayAgain}
            style={{ marginRight: 16 }}
          >
            
          </Button>
          <Button variant="outlined" color="primary" onClick={handleTryNewLevel}>
            
          </Button>
        </Box>
      </Box>
    );
  };

  // 
  const renderLevelSelection = () => {
    const currentGame = games[selectedGame];

    return (
      <Box>
        <Button
          startIcon={<ArrowBack />}
          onClick={() => setShowLevelSelection(false)}
          className={classes.backButton}
        >
          
        </Button>

        <Typography variant="h5" gutterBottom>
           {currentGame.name} 
        </Typography>

        <Grid container spacing={3}>
          {currentGame.levels.map((level) => (
            <Grid item xs={12} sm={4} key={level.level}>
              <Card className={classes.levelCard}>
                <CardContent className={classes.levelCardContent}>
                  <Typography variant="h6" gutterBottom>
                    {level.name}
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    {level.description}
                  </Typography>
                </CardContent>
                <CardActions className={classes.levelCardActions}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => handleLevelSelect(level.level)}
                  >
                    
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Box>
    );
  };

  // 
  const renderGameComponent = () => {
    const currentGame = games[selectedGame];
    const GameComponent = currentGame.component;

    return (
      <Box>
        <Button
          startIcon={<ArrowBack />}
          onClick={handleBackToLevelSelection}
          className={classes.backButton}
        >
          
        </Button>

        <GameComponent level={selectedLevel} onComplete={handleGameComplete} />
      </Box>
    );
  };

  // 
  const renderGameSelection = () => {
    const currentGame = games[selectedGame];

    return (
      <Box>
        <Typography variant="h5" gutterBottom>
          {currentGame.name}
        </Typography>
        <Typography variant="body1" paragraph>
          {currentGame.description}
        </Typography>
        <Box display="flex" justifyContent="center" mt={2}>
          {currentGame.icon}
        </Box>
        <Box display="flex" justifyContent="center" mt={3}>
          <Button variant="contained" color="primary" onClick={handleStartGame}>
            
          </Button>
        </Box>
      </Box>
    );
  };

  return (
    <div className={classes.root}>
      <Paper>
        <Tabs
          value={selectedGame}
          onChange={handleGameChange}
          indicatorColor="primary"
          textColor="primary"
          centered
        >
          {games.map((game, index) => (
            <Tab key={game.id} label={game.name} icon={React.cloneElement(game.icon, { style: { fontSize: 24 } })} />
          ))}
        </Tabs>

        <Box p={3} className={classes.gameContainer}>
          {showResult
            ? renderGameResult()
            : selectedLevel
            ? renderGameComponent()
            : showLevelSelection
            ? renderLevelSelection()
            : renderGameSelection()}
        </Box>
      </Paper>
    </div>
  );
};

export default GameSelector;